.landing-category-container {
  height: 100%;
  max-width: 400px;
  max-height: 406px;
  transform: translateX(-85px);
  display: flex;
  justify-content: center; }
  @media screen and (max-width: 1200px) {
    .landing-category-container {
      transform: translateX(-45px); } }
  @media screen and (max-width: 991px) {
    .landing-category-container {
      margin: 50px 0;
      padding: 0 15px;
      transform: none; } }
  .landing-category-container h3 {
    font-size: 16px;
    color: #6480EB;
    font-weight: bold; }
  .landing-category-container .button-container {
    display: flex; }
    .landing-category-container .button-container:nth-of-type(even) {
      justify-content: flex-end; }
    .landing-category-container .button-container:nth-of-type(odd) {
      justify-content: flex-start; }
    .landing-category-container .button-container button {
      transition: 0.3s ease; }
      .landing-category-container .button-container button:hover {
        transform: scale(1.1); }
      .landing-category-container .button-container button:active {
        transform: scale(1); }
  .landing-category-container .cat-button button {
    border-radius: 20px; }
    .landing-category-container .cat-button button:before {
      border-radius: 20px; }
  .landing-category-container .cat-button span {
    margin-left: 10px; }
